.header {
  width: 100%;
  height: 60px;
  background: #222;
  .headerWrap {
    width: 1150px;
    margin: 0 auto;
    .logo {
      position: absolute;
      top: 12px;
      width: 194px;
      height: 35px;
      background-image: url('../../assets/header/logo-about.png');
      background-size: contain;
      background-repeat: no-repeat;
      display: inline-block;
    }
    ul {
      float: right;
      li {
        color: #FABE00;
        display: inline-block;
        font-size: 16px;
        line-height: 58px;
        margin: 0 25px;
        a {
          display: block;
          height: 45px;
          &:hover {
            border-bottom: 2px solid #FABE00;
          }
        }
      }
    }
  }
}
.aboutUs {
  overflow: hidden;
  width: 100%;
  background: url('../../assets/about/banner-bg.jpg') no-repeat center top #ddd;
  background-size: 1500px;
  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 845px;
    background: url('../../assets/about/banner-bg.svg') no-repeat center top;
  }
  .aboutUsWrap {
    overflow: hidden;
    width: 580px;
    margin: -74px auto 0;
    text-align: center;
    .aboutUsTitle {
      margin: 0 auto;
      h2, h3 {
        text-align: center;
        color: #272727;
      }
      h2 {
        font-size: 24px;
      }
      h3 {
        font-size: 20px;
        margin: 2px auto 18px;
      }
    }
    .aboutUsContent {
      width: 270px;
      float: left;
      margin-top: 37px;
      p {
        color: #191919;
        margin-bottom: 16px;
        text-align: justify;
        line-height: 20px;
      }
    }
    img {
      float: left;
      margin-right: 22px;
      width: 288px;
      height: auto;
    }
  }
  .companyBgWrap {
    clear: both;
    width: 1150px;
    margin: 68px auto 20px;
    .companyBgTitle {
      h2, h3 {
        text-align: center;
        color: #272727;
      }
      h2 {
        font-size: 24px;
      }
      h3 {
        font-size: 20px;
        margin: 2px auto 26px;
      }
    }
    p {
      color: #4a4a4a;
      width: 750px;
      margin: 0 auto 40px;
      line-height: 18px;
    }
    ul {
      overflow: hidden;
      text-align: center;
      li {
        display: inline-block;
        border-left: 1px dashed #fff;
        &:first-child {
          border-left: none;
        }
        img {
          display: block;
          margin: 0 15px 13px;
        }
        span {
          font-size: 16px;
          color: #4a4a4a;
        }
      }
    }
  }
}
.timeLine {
  width: 100%;
  height: 804px;
  background: url('../../assets/about/time-line.svg') no-repeat center center #efefef;
  .timeLineWrap {
    width: 1150px;
    margin: 0 auto;
    position: relative;
    .timeLineMonth {
      position: absolute;
      width: 250px;
      font-size: 18px;
      color: #9B9B9B;
    }
    .timeLineEvent {
      position: absolute;
      width: 250px;
      font-size: 12px;
      color: #4A4A4A;
      line-height: 18px;
    }
    h4 {
      position: absolute;
      font-size: 16px;
      color: #4A4A4A;
    }
  }
}
.related {
  width: 1150px;
  height: 800px;
  margin: 0 auto;
  .relatedTitle {
    h2, h3 {
      text-align: center;
      color: #272727;
    }
    h2 {
      font-size: 24px;
      margin-top: 60px;
    }
    h3 {
      font-size: 20px;
      margin: 2px auto 50px;
    }
  }
  ul {
    overflow: hidden;
    text-align: center;
    li {
      float: left;
      margin: 0 7px;
      img {
        display: block;
      }
      span {
        font-size: 16px;
        color: #4a4a4a;
      }
      div {
        overflow: hidden;
        width: 214px;
        height: 70px;
        background: #efefef;
        margin-top: 16px;
        h4 {
          font-size: 16px;
          color: #4A4A4A;
          margin: 8px 0 6px;
        }
        p {
          font-size: 12px;
          color: #4A4A4A;
          line-height: 16px;
        }
      }
    }
  }
  .partnersTitle {
    h2, h3 {
      text-align: center;
      color: #272727;
    }
    h2 {
      font-size: 24px;
      margin-top: 60px;
    }
    h3 {
      font-size: 20px;
      margin: 2px auto 40px;
    }
  }
  .partners {
    width: 770px;
    margin: 0 auto;
    color: #4A4A4A;
    line-height: 18px;
  }
}
.joinUs {
  overflow: hidden;
  background: #efefef;
  .joinUsTitle {
    h2, h3 {
      text-align: center;
      color: #272727;
    }
    h2 {
      font-size: 24px;
      margin-top: 60px;
    }
    h3 {
      font-size: 20px;
      margin: 2px auto 40px;
    }
  }
  .joinUsSummary {
    width: 755px;
    margin: 0 auto;
    color: #4A4A4A;
    line-height: 18px;
    text-align: justify;
    margin-bottom: 64px;
  }
  .joinUsContent {
    width: 750px;
    margin: 0 auto 170px;
    position: relative;
    .joinUsContentLeft, .joinUsContentRight {
      font-size: 40px;
      position: absolute;
      top: 100px;
      cursor: pointer;
    }
    .joinUsContentLeft {
      left: 0;
    }
    .joinUsContentRight {
      right: 0;
    }
    ul {
      width: 550px;
      margin: 0 auto;
      li {
        background: #fff;
        max-width: 550px;
        padding: 16px 25px;
        h4 {
          font-size: 16px;
          color: #F1B701;
        }
        h6 {
          color: #4a4a4a;
          font-weight: normal;
          font-size: 14px;
          margin: 10px auto 4px;
        }
        p {
          color: #4a4a4a;
          font-size: 12px;
          line-height: 18px;
        }
      }
    }
  }
}
.contactUs {
  overflow: hidden;
  .contactUsWrap {
    width: 750px;
    margin: 0 auto;
    .contactUsTitle {
      h2, h3 {
        text-align: center;
        color: #272727;
      }
      h2 {
        font-size: 24px;
        margin-top: 60px;
      }
      h3 {
        font-size: 20px;
        margin: 2px auto 36px;
      }
    }
    .contactUsInfo {
      overflow: hidden;
      width: 380px;
      height: 369px;
      float: left;
      .contactUsInfoTitle {
        margin-top: 14px;
        color: #4A4A4A;
        line-height: 32px;
        font-size: 16px;
      }
      .contactUsTel {
        font-size: 20px;
        color: #000;
        line-height: 32px;
      }
      ul {
        margin-top: 30px;
        li {
          float: left;
          margin-right: 20px;
          span {
            display: block;
            margin-top: 10px;
            text-align: center;
          }
        }
      }
    }
    .contactUsMap {
      position: relative;
      width: 363px;
      height: 369px;
      background: url('../../assets/about/address-map.jpg') no-repeat;
      background-size: 100% 100%;
      float: right;
      a {
        display: block;
        width: 100%;
        height: 100%;
      }
      img {
        position: absolute;
      }
      .contactUsMapLoc {
        top: 130px;
        left: 253px;
      }
      .contactUsMapLogo {
        width: 82px;
        top: 180px;
        left: 226px;
      }
    }
    .contactUsAddress {
      border-top: 1px solid #EFEFEF;
      padding: 12px 0 40px;
      clear: both;
      span {
        margin-right: 45px;
        label {
          font-weight: bold;
          color: #4a4a4a;
        }
      }
    }
  }
}
.footer {
  background: #222222;
  height: 120px;
  width: 100%;
  overflow: hidden;
  .copy-right {
    width: 300px;
    height: 80px;
    font-size: 15px;
    color: #9A9A9A;
    margin: 20px auto;
    text-align: center;
    ul {
      width: 265px;
      margin: 0 auto;
      overflow: hidden;
      li {
        float: left;
        a:hover {
          color: #FABE00;
        }
      }
      li:nth-child(2) {
        margin: 0 18px;
      }
      li:nth-child(4) {
        margin: 0 18px;
      }
    }
    p {
      font-size: 13px;
      margin-top: 16px;
      line-height: 18px;
      color: #595959;
    }
    p:last-child {
      margin-top: 5px;
      line-height: 18px;
    }
  }
}
</style>



